	    gl.viewport(0, 0, filterArea.width, filterArea.height);
	
	    projection.x = filterArea.width/2;
	    projection.y = -filterArea.height/2;
	
	    offset.x = -filterArea.x;
	    offset.y = -filterArea.y;
	
